@import '../../../../assets/style/variable';
@import '../../../../assets/style/mixins.scss';

.wrapper {
  display: flex;
  flex-direction: column;
  width: 320px;
  margin: 0 auto 20px;
  border-radius: 15px;
  background: var(--darkBlue);
  color: var(--white);
  box-sizing: border-box;

  @include onTablet {
    flex: 1 1 45%;
    margin: 10px;
  }

  @include onNotebook {
    flex-wrap: wrap;
    flex: 1 1 20%;
    margin: 10px;
  }
}

.image {
  flex: 0 0 178px;
  width: 100%;
  border-radius: 15px;
  margin-bottom: 20px;
}

.image img {
  border-radius: 15px;
  width: 100%;
  height: 100%;
}

.text {
  flex: 1 1 1fr;
  padding: 0 20px;
  font-size: 14x;
}

.title {
  font-size: 18px;
  color: var(--white);
}

.text h2 {
  margin: 0 auto;
  padding: 0;
}

.info {
  flex: 1 1 30px;
  display: flex;
  margin-left: 8px;
}

.info span {
  margin-right: 20px;
}

.eyeIcon {
  width: 19px;
  height: 14px;
  margin-right: 8px;
}

.calendarIcon {
  width: 16px;
  height: 15px;
  margin-right: 8px;
}