@import '../../assets/style/variable';
@import '../../assets/style/mixins.scss';

.dashboard {
  background: var(--background);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 67px 10px 10px;
  height: 100%;


  @include onTablet {
    flex-direction: row;
    flex-wrap: wrap;
    padding-left: 70px
  }

  @include onNotebook {
    flex-direction: row;
    flex-wrap: wrap;
    padding-left: 70px;
  }

  @include onFull {
    padding-left: 123px;
  }
}

ol, ul {
	list-style: none;
}

.balance {
  flex: 1 1 100%;
  display: flex;
  flex-direction: column;

  @include onTablet {
    flex-direction: row;
  }

  @include onNotebook {
    flex-direction: row;
    flex: 1 1 45%;
  }
}

.otherBlocks {
  flex: 1 1 100%;
  align-self: center;

  @include onNotebook {
    flex: 1 1 45%;
  }
}

.contracts {
  flex: 1 1 100%;
  display: flex;
  flex-direction: column;

  @include onTablet {
    flex-direction: row;
  }

  @include onNotebook {
    flex-direction: row;
    flex: 1 1 50%;
  }
}

.other {
  flex: 1 1 100%;
  display: flex;
  flex-direction: column;

  @include onTablet {
    flex-direction: row;
  }

  @include onNotebook {
    flex-direction: row;
    flex: 1 1 50%;
  }
}
