@import '../../../../assets/style/variable';
@import '../../../../assets/style/mixins.scss';

.nav {
  display: flex;
  flex-direction: column;
  width: 112px;
  height: 100vh;
  background: var(--gradienBlack);
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  overflow: hidden;
  transition: 300ms;

  &:hover {
    width: 232px;
  }

  @include onTablet {
    width: 67px;
  }

  @include onNotebook {
    width: 67px;
  }

  @include onFull {
    width: 112px;
  }
}

.logo {
  text-align: center;
  padding-top: 38px;
  padding-bottom: 38px;

}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  display: flex;
  width: 100%;
  height: 70px;
  position: relative;
  text-align: left;
  vertical-align: middle;
}


.dashboardIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 20px;
  }

  @include onNotebook {
    padding-left: 20px;
  }

  @include onFull {
    padding-left: 40px;
  }
}

.contactsIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 23px;
  }

  @include onNotebook {
    padding-left: 23px;
  }

  @include onFull {
    padding-left: 43px;
  }
}

.structureIcon, .calendarIcon, .promoIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 22px;
  }

  @include onNotebook {
    padding-left: 22px;
  }

  @include onFull {
    padding-left: 42px;
  }
}

.partnersIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 22px;
  }

  @include onNotebook {
    padding-left: 22px;
  }

  @include onFull {
    padding-left: 42px;
  }
}

.text {
  color: var(--white);
  padding-top: 27px;
}

.history {
  padding-left: 30px;
  margin-top: auto;
  margin-bottom: 15px;
  display: flex;
  align-items: center;

  @include onTablet {
    margin-bottom: 18px;
    width: 41px;
    height: 41px;
    padding-left: 10px;
  }

  @include onNotebook {
    margin-bottom: 18px;
    width: 41px;
    height: 41px;
    padding-left: 10px;
  }

  @include onFull {
    margin-bottom: 18px;
    width: 52px;
    height: 52px;
    padding-left: 30px;
  }
}

.active {
  background-color: rgb(14%, 74%, 98%, 0.15);;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;

  &::after {
    content: '';
    position: absolute;
    border-color: var(--blue);
    border-style: solid;
    border-width: 0 2px 0 0;
    border-radius: 10px;
    height: 100%;
    top: 0;
    left: 0;
    width: 1px;
  }

  &::before {
    content: '';
    position: absolute;
    border-color: var(--blue);
    border-style: solid;
    border-width: 0 3px 0 0;
    border-radius: 15px;
    height: 60%;
    top: 20%;
    right: 0;
    width: 1px;
  }
}
