@import '../../../../assets/style/variable';
@import '../../../../assets/style/mixins.scss';

.nav {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: var(--gradienBlack);
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  overflow: hidden;
  transition: 300ms;

  @include onTablet {
    width: 163px;
  }

  @include onNotebook {
    width: 163px;
  }

  @include onFull {
    &:hover {
      width: 232px;
    }
  }
}

.logo {
  text-align: center;
  padding-top: 38px;
  padding-bottom: 24px;
  height: 43.5px;

  @include onTablet {
  }



  @include onFull {
    text-align: center;
    padding-top: 38px;
    padding-bottom: 24px;
  }
  
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;

  @include onTablet {
  }

  
  @include onFull {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}

.item {
  display: flex;
  position: relative;
  cursor: pointer;
  width: 100%;
  height: 70px;
  text-align: left;
  vertical-align: middle;

  &:hover .text {
    color: var(--blue);
  }

  &:target {
    background-color: rgb(14%, 74%, 98%, 0.15);;
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
  }

  @include onTablet {
  }

  @include onFull {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      height: 70px;
      width: 10px;
      color: var(--blue);
    }
  

  }
}

.dashboardIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 20px;
    vertical-align: middle;
  }

  @include onNotebook {
    padding-left: 20px;
    vertical-align: middle;
  }

  @include onFull {
    padding-left: 40px;
  }
}

.contactsIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 23px;
  }

  @include onNotebook {
    padding-left: 23px;
  }

  @include onFull {
    padding-left: 43px;
  }
}

.structureIcon, .calendarIcon, .promoIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 22px;
  }

  @include onNotebook {
    padding-left: 22px;
  }

  @include onFull {
    padding-left: 42px;
  }
}

.partnersIcon {
  display: flex;
  align-items: center;

  @include onTablet {
    padding-left: 22px;
  }

  @include onNotebook {
    padding-left: 22px;
  }

  @include onFull {
    padding-left: 41px;
  }
}

.text {
  color: var(--white);
  padding-left: 16px;
  display: flex;
  align-items: center;

  @include onTablet {
  }

  @include onFull {
    padding-left: 16px;
  }
}

.history {
  margin-top: auto;
  cursor: pointer;
  margin-bottom: 15px;

  @include onTablet {
    padding-left: 10px;
  }

  @include onNotebook {
    padding-left: 10px;
  }

  @include onFull {
    padding-left: 30px;
  }
}

.historyIcon {
  display: flex;
  align-items: center;
  width: 140px;
  height: 47px;
}

.active {
  background-color: rgb(14%, 74%, 98%, 0.15);;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;

  &::after {
    content: '';
    position: absolute;
    border-color: var(--blue);
    border-style: solid;
    border-width: 0 2px 0 0;
    border-radius: 10px;
    height: 100%;
    top: 0;
    left: 0;
    width: 1px;
  }

  &::before {
    content: '';
    position: absolute;
    border-color: var(--blue);
    border-style: solid;
    border-width: 0 3px 0 0;
    border-radius: 15px;
    height: 60%;
    top: 20%;
    right: 0;
    width: 1px;
  }
}
