@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.wrapper {
  box-sizing: border-box;

  @include onTablet {
    margin: 10px;
    flex: 1 1 50%;
  }

  @include onNotebook {
    margin: 10px;
    flex: 1 1 50%;
  }
}

.ReferralProfit {
  color: var(--white);
  background: var(--darkBlue);
  width: 320px;
  height: 173px;
  border-radius: 15px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;

  @include onTablet {
    width: 100%;
  }
  
  @include onNotebook {
    width: 100%;
  }
}

.iconBox {
  display: flex;
  width: 49px;
  height: 49px;
  border-radius: 15px;
  background: rgb(44%, 90%, 88%, 0.1);
  justify-content: center;
  align-items: center;
  margin: 0 auto 20px;

  @include onNotebook {
    margin: 0 0 20px 0;
  }
}

.ReferralProfitIcon {
  width: 25px;
  height: 25px;
}

.RZMBalanceIcon {
  width: 29px;
  height: 29px;
  margin-left: 10px;
}

.sum {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: 36px;
  margin-bottom: 5px;

  @include onNotebook {
    justify-content: start;
  }
}

.text {
  text-align: center;
  font-size: 12px;
  color: var(--blue);

  @include onNotebook {
    text-align: left;
  }
}

.text span {
  color: var(--white);
}