@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  @include onTablet {
    margin-left: 80px;
  }

  @include onNotebook {
    margin-left: 80px;
  }

  @include onFull {
    margin-left: 125px;
  }
}

header {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
  height: 62px;
  background: var(--background);
  align-items: center;
  border-bottom: 1px solid var(--greyDark);
  border-left: none;
  box-sizing: border-box;
  color: ver(--white);
}

.profile {
  flex: 0 0 25%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.profilePhoto {
  border-radius: 50%;
  width: auto;
  height: 32px;
}

.profile span {
  padding-left: 8px;
  color: var(--white);
}

.smallLogoIcon {
  flex: 0 0 50%;

  @include onTablet {
    display: none;
  }
}
.smallLogoIcon div{
  display: flex;
  align-items: center;
}

.navigation {
  flex: 0 0 25%;
  display: flex;
  justify-content: flex-end;

  @include onTablet {
    display: none;
  }

  @include upHorizontalTablet {
    display: none;
  }
}

.navigation div {
  display: flex;
  align-items: center;
}

.otherInformation {
  display: none;
}

.currencyExchange {
  display: none;
}

.arrowIcon {
  display: none;
}

.language {
  display: none;
}

.notificationsIcon {
  display: none;
}

.leftPanel {
  display: none;
}

.arrowToolsIcon {
  display: none;
}

.CoinIcon {
  width: 18px;
  height: 18px;
  margin-right: 8px;
}

.DNSCoinIcon {
  width: 18px;
  height: 18px;
  margin-right: 5px;
}

.notificationsIconMobile {
  margin-right: 23px;
}

@include onTablet {
  .info {
    display: flex;
    margin-right: 25px;
  }

  .currencyExchange {
    display: flex;
  }

  .currencyExchangeRZM {
    display: none;
  }

  .otherInformation {
    font-size: 14px;
    display: flex;
    color: var(--white);
    margin-right: 30px;
    align-items: center;
  }

  .calendarIcon {
    margin-right: 8px;
    align-items: center;
    height: 30px;
  }

  .date {
    display: flex;
    flex-direction: column;
  };

  .date span {
    font-size: 10px;
  }

  .currencyExchangeDNC {
    display: flex;
    color: var(--white);
    align-items: center;
  }

  .vectorIcon {
    margin-left: 10px;
    margin-right: 10px;
  }

  .logo {
    display: none;
  }

  .tetherICon {
    width: 18px;
    height: 18px;
    margin-right: 5px;
  }

  .arrowIcon {
    margin-left: 8px;
    display: block;
  }

  .rightPanel {
    display: flex;
    margin-right: 25px;
    align-items: center;
  }

  .language {
    color: var(--blue);
    display: inline-block;
    margin-right: 30px;
  }

  .leftPanel {
    display: flex;
    align-items: center;
  }

  .notificationsIcon {
    margin-right: 30px;
    height: 22px;
  }

  .arrowToolsIcon {
    display: block;
    margin-left: 8px;
  }

  .weather {
    display: none
  }
  
  .cloudIcon {
    display: none
  }
  
  .clockIcon {
    display: none
  }
  
  .time {
    display: none
  }
}

