@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.button {
  background: var(--green);
  font-size: 18px;
  color: var(--white);
  border-radius: 15px;
  border: none;
  width: 100%;
  height: 100%;
  cursor: pointer;

  &:hover {
    background: var(--blue);
  }

  &:target {
    background-color: #219DD5;
  }
}

.button span {
  font-size: 14px;
}
