@import '../../../../assets/style/variable';

.nav {
  position: absolute;
  display: flex;
  flex-direction: column;
  right: 0;
  top: 0;
  width: 233px;
  height: 100vh;
  background: var(--gradienBlack);
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  overflow: hidden;
  transition: 300ms;
  font-size: 18px;
}

.logo {
  display: flex;
  text-align: center;
  padding-top: 38px;
  padding-bottom: 24px;
}

.closeMenu div {
  display: flex;
  align-items: center;
  padding-left: 56px;
}

.MobileLogoIcon {
  width: 98px;
  height: 19px;
  padding-left: 40px;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 77px;
}

.item {
  display: flex;
  cursor: pointer;
  width: 100%;
  height: 70px;
  text-align: left;
  vertical-align: middle;
  position: relative;
}

.active {
  background-color: rgb(14%, 74%, 98%, 0.15);;
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  box-shadow: 2px 3px 4px black;
  
  &::after {
    content: '';
    position: absolute;
    border-color: var(--blue);
    border-style: solid;
    border-width: 0 3px 0 0;
    border-radius: 15px;
    height: 100%;
    top: 0%;
    right: 0;
    width: 1px;
  }

  &::before {
    content: '';
    position: absolute;
    border-color: var(--blue);
    border-style: solid;
    border-width: 0 2px 0 0;
    border-radius: 10px;
    height: 60%;
    top: 20%;
    left: 0;
    width: 1px;
  }
}

.dashboardIcon {
  padding-top: 22px;
  padding-left: 40px;
}

.contactsIcon {
  padding-top: 22px;
  padding-left: 43px;
}

.structureIcon, .calendarIcon, .promoIcon {
  padding-top: 22px;
  padding-left: 42px;
}

.partnersIcon {
  padding-top: 22px;
  padding-left: 41px;
}

.text {
  color: var(--white);
  padding-left: 16px;
  padding-top: 27px;
}

.history {
  padding-left: 30px;
  width: 176px;
  height: 52px;
  margin-top: auto;
  cursor: pointer;
  margin-bottom: 15px;
}
