@import '../../../assets/style/variable';
@import '../../../assets/style/mixins.scss';

.wrapper {
  box-sizing: border-box;

  @include onTablet {
    margin: 10px;
    flex: 1 1 50%;
  }

  
  @include onNotebook {
    margin: 10px;
    flex: 1 1 50%;
  }
}

.DNCBalance {
  height: 418px;
  width: 320px;
  background: var(--gradienBlack);
  border-radius: 15px;
  margin: 0 auto 20px;
  color: var(--white);

  @include onTablet {
    width: 100%;
    height: 366px;
  }

  @include onNotebook {
    width: 100%;
    height: 366px;
    margin: 0 auto;
  }
}

.blockName {
  display: flex;
  justify-content: center;
  margin: 0 auto 3px;
  padding-top: 27px;
}

.DNCBalanceIcon {
  width: 20px;
  height: 20px;
}

.blockName span {
  margin-left: 5px;
}

.balance {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-size: 36px;
  line-height: 36px;
  margin-bottom: 12px;
}

.todayIncome {
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  color: var(--blue);
}

.todayIncome span {
  font-weight: normal;
  color: var(--white);
}

.shedule {
  margin-top: 20px;
  text-align: center;
  font-size: 48px;
  color: var(--blue);
}

.numbers {
  margin-bottom: 18px;
  text-align: center;
}

.buttons {
  display: flex;
  flex-direction: column;
  text-align: center;


  @include onTablet {
    flex-direction: row;
    justify-content: space-around;
  }

  @include onNotebook {
    flex-direction: row;
    justify-content: space-around;
  }
}

.button {
  width: 290px;
  height: 47px;
  font-size: 14px;
  text-align: center;
  margin: 0 auto 10px;

  @include onTablet {
    width: 100%;
    margin: 0 20px 10px 20px;
    height: 47px;
  }

  @include onNotebook {
    margin: 0 20px 10px 20px;
    width: 100%;
    height: 47px;
  }
}
